import { createRouter, createWebHistory } from 'vue-router';
import home_page from '../components/home_page.vue';
import pay_page from '../components/pay_page.vue';
import order_page from '../components/order_page.vue';
import shop_page from '../components/shop_page.vue';
import other_page from '../components/other_page.vue';
const routes = [
  {
    path: '/',
    name: 'home_page',
    component: home_page
  },
  {
    path: '/pay',
    name: 'pay_page',
    component: pay_page
  },
  {
    path: '/order',
    name: 'order_page',
    component: order_page
  },
  {
    path: '/shop',
    name: 'shop_page',
    component: shop_page
  },
  {
    path: '/other',
    name: 'other_page',
    component: other_page
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
